import React from "react";
import Button from "@material-ui/core/Button";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";

import { Link } from "gatsby";

import FootballSim from "../images/MOS.jpg";
import Metiri from "../images/METIRI.jpg";
import Raytracer from "../images/MCRT.jpg";
import Amaze from "../images/AMAZE.jpg";
import Face from "../images/FACE.jpg";
import Bibliu from "../images/BIBLIU.jpg";
import Proc from "../images/PROC.png";
import Cloudmatch from "../images/CLOUDMATCH.png";

//import Code from "@material-ui/icons/Code";

const cards2020 = [
  {
    id: "1",
    content: {
      title: "Proccraft",
      image: Proc,
      description: "Voxel engine for generation of procedural terrains.",
      source: true,
      sourceLink: "https://github.com/FredrikErikJohansson/Proccraft",
    },
  },
  {
    id: "2",
    content: {
      title: "Cloudmatch",
      image: Cloudmatch,
      description: "Spotify to Soundcloud music recommendation tool.",
      source: true,
      sourceLink: "https://github.com/johanforslund/cloudmatch",
    },
  },
];

const cards2019 = [
  {
    id: "1",
    content: {
      title: "Face Recognition Software",
      image: Face,
      description: "Software which identifies faces using image processing.",
      source: true,
      sourceLink: "https://github.com/samuelllsvensson/Face_Recognition",
    },
  },
  {
    id: "2",
    content: {
      title: "Monte Carlo Ray Tracer",
      image: Raytracer,
      description: "Monte Carlo Ray Tracer implemented in C++.",
      source: true,
      sourceLink: "https://github.com/FredrikErikJohansson/Ray-tracer",
    },
  },
  {
    id: "3",
    content: {
      title: "Metiri",
      image: Metiri,
      description: "Android application to measure distances using ARcore.",
      source: false,
    },
  },
  {
    id: "4",
    content: {
      title: "Football Simulator",
      image: FootballSim,
      description: "Football Trajectory Simulator with Drag and Magnus effect.",
      source: true,
      sourceLink: "https://github.com/FredrikErikJohansson/FootballSim",
    },
  },
  {
    id: "5",
    content: {
      title: "Bibliuteket",
      image: Bibliu,
      description: "Android/iOS application for selling course literature.",
      source: true,
      sourceLink: "https://github.com/johanforslund/bibliuteket",
    },
  },
];

const cards2018 = [
  {
    id: "1",
    content: {
      title: "Amaze",
      image: Amaze,
      description:
        "Mobile game for Samsung Gear VR, developed in Unreal Engine.",
      source: true,
      sourceLink: "https://github.com/FredrikErikJohansson/vr-maze",
    },
  },
];

class ProjectCardList extends React.Component {
  handleSource = (content) => {
    if (content.source) {
      return (
        <Button
          size="small"
          color="primary"
          rel="noopener noreferrer"
          target="_blank"
          href={content.sourceLink}
        >
          Github
        </Button>
      );
    }
  };

  render() {
    return (
      <div>
        <Container style={{ paddingTop: 16, paddingBottom: 48 }} maxWidth="md">
          <Typography component="h4" variant="h4" gutterBottom color="primary">
            2020
          </Typography>
          <Grid container spacing={4}>
            {cards2020.map((card) => (
              <Grid item key={card.id} xs={12} sm={6} md={4}>
                <Card
                  style={{
                    backgroundColor: "#fff",
                    height: "100%",
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <Link
                    to="/project"
                    state={{
                      title: card.content.title,
                      bodyText: card.content.bodyText,
                    }}
                    activeStyle={{ color: "#fff" }}
                    style={{ color: "#000", textDecoration: "inherit" }}
                  >
                    <CardActionArea>
                      <CardMedia
                        image={card.content.image}
                        title={card.content.title}
                        style={{ height: 0, paddingTop: "56.25%" }}
                      />
                      <CardContent style={{ flexGrow: 1 }}>
                        <Typography gutterBottom variant="h6" color="primary">
                          {card.content.title}
                        </Typography>
                        <Typography color="primary" variant="subtitle1">
                          {card.content.description}
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                  </Link>
                  <CardActions>
                    <Link
                      to="/project"
                      state={{
                        title: card.content.title,
                        bodyText: card.content.bodyText,
                      }}
                      activeStyle={{ color: "#fff" }}
                      style={{ color: "#000", textDecoration: "inherit" }}
                    >
                      <Button size="small" color="primary">
                        View
                      </Button>
                    </Link>
                    {this.handleSource(card.content)}
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
        <Container style={{ paddingTop: 16, paddingBottom: 48 }} maxWidth="md">
          <Typography component="h4" variant="h4" gutterBottom color="primary">
            2019
          </Typography>
          <Grid container spacing={4}>
            {cards2019.map((card) => (
              <Grid item key={card.id} xs={12} sm={6} md={4}>
                <Card
                  style={{
                    backgroundColor: "#fff",
                    height: "100%",
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <Link
                    to="/project"
                    state={{
                      title: card.content.title,
                      bodyText: card.content.bodyText,
                    }}
                    activeStyle={{ color: "#fff" }}
                    style={{ color: "#000", textDecoration: "inherit" }}
                  >
                    <CardActionArea>
                      <CardMedia
                        image={card.content.image}
                        title={card.content.title}
                        style={{ height: 0, paddingTop: "56.25%" }}
                      />
                      <CardContent style={{ flexGrow: 1 }}>
                        <Typography gutterBottom variant="h6" color="primary">
                          {card.content.title}
                        </Typography>
                        <Typography color="primary" variant="subtitle1">
                          {card.content.description}
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                  </Link>
                  <CardActions>
                    <Link
                      to="/project"
                      state={{
                        title: card.content.title,
                        bodyText: card.content.bodyText,
                      }}
                      activeStyle={{ color: "#fff" }}
                      style={{ color: "#000", textDecoration: "inherit" }}
                    >
                      <Button size="small" color="primary">
                        View
                      </Button>
                    </Link>
                    {this.handleSource(card.content)}
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
        <Container style={{ paddingTop: 16, paddingBottom: 48 }} maxWidth="md">
          <Typography component="h4" variant="h4" gutterBottom color="primary">
            2018
          </Typography>
          <Grid container spacing={4}>
            {cards2018.map((card) => (
              <Grid item key={card.id} xs={12} sm={6} md={4}>
                <Card
                  style={{
                    backgroundColor: "#fff",
                    height: "100%",
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <Link
                    to="/project"
                    state={{
                      title: card.content.title,
                      bodyText: card.content.bodyText,
                    }}
                    activeStyle={{ color: "#fff" }}
                    style={{ color: "#000", textDecoration: "inherit" }}
                  >
                    <CardActionArea>
                      <CardMedia
                        image={card.content.image}
                        title={card.content.title}
                        style={{ height: 0, paddingTop: "56.25%" }}
                      />
                      <CardContent style={{ flexGrow: 1 }}>
                        <Typography gutterBottom variant="h6" color="primary">
                          {card.content.title}
                        </Typography>
                        <Typography color="primary" variant="subtitle1">
                          {card.content.description}
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                  </Link>
                  <CardActions>
                    <Link
                      to="/project"
                      state={{
                        title: card.content.title,
                        bodyText: card.content.bodyText,
                      }}
                      activeStyle={{ color: "#fff" }}
                      style={{ color: "#000", textDecoration: "inherit" }}
                    >
                      <Button size="small" color="primary">
                        View
                      </Button>
                    </Link>
                    {this.handleSource(card.content)}
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
      </div>
    );
  }
}

export default ProjectCardList;
